html, body {
  min-height: 100%;
}

body {
  background: linear-gradient(167.96deg, #f4f9ff 0%, #e0edfb 100%);
  font-family: "Inter", "Open Sans", sans-serif;
}

input {
  outline: none;
}

input {
  border: 2px solid #151d5133;
  border-radius: 10px;
  color: #151d51;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  padding: 10px 20px 10px 20px;
}

input:focus {
  border: 2px solid #151d51;
}

input:disabled {
}

input.valid {
  background: #5cd6c01a;
  border: 2px solid #5cd6c0;
}

input.invalid {
  border: 2px solid #ed5f59;
  background: #ed5f591a;
}

button[type="submit"] {
  padding: 15px 32px 15px 32px;
  background: linear-gradient(110.46deg, #70c3ff 12.27%, #4b65ff 93.92%);
  border: 0;
  border-radius: 30px;
  font-family: Inter;
  font-weight: 700;
  font-size: 16px;
  line-height: 14px;
  color: #ffffff;
  cursor: pointer;
}

button[type="button"] {
  position: absolute;
  right: 24px;
  background-color: transparent;
  border: 0px;
  cursor: pointer;
}

::placeholder {
  color: #151d51b2;
}

p {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

p.valid {
  color: #009796;
}